.bestseller-category {
    display: flex;
    flex-direction: column;
}

.category-blocks-list {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.main-title {
    font-weight: $bold;

    font-size: 48px;
    line-height: 52px;
    letter-spacing: -0.04em;
    color: var(--text-color);
    margin-bottom: 40px;

    transition: color 200ms;

    @media screen and (max-width: ($tablet - 1)) {
        font-size: 32px;
        line-height: 38px;
    }
}

.main-title--last-word-static {
    color: $violet;
    transition: color 200ms;
}

.bestseller-category-title {
    font-size: 14px;
    line-height: 18px;

    letter-spacing: 0.03em;
    text-transform: uppercase;

    color: $gray;

    margin-bottom: 18px;

    @media screen and (min-width: $tablet) {
        line-height: 24px;
    }
}

.books-list {
    display: flex;
    gap: 24px;

    margin-bottom: 32px;

    @media screen and (max-width: ($tablet - 1)) {
        margin-bottom: 8px;
        flex-direction: column;
    }

    @media screen and (min-width: $tablet) and (max-width: ($desktop - 1)) {
        margin-bottom: 20px;
    }
}

.book-image {
    object-fit: fill;
    // width: 335px;

    min-width: 335px;
    height: 485px;

    @media screen and (min-width: $tablet) {
        min-width: 218px;
        height: 316px;
    }

    @media screen and (min-width: $desktop) {
        min-width: 180px;
        height: 256px;
    }
}

.book-title {
    font-size: 16px;
    font-weight: $bold;
    line-height: 18px;
    letter-spacing: -0.04em;
    color: var(--text-color);
    max-width: 180px;

    margin-bottom: 4px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.book-author {
    font-size: 12px;
    font-weight: $regular;
    line-height: 14px;
    letter-spacing: -0.04em;

    font-style: italic;
    color: $gray;
}

.book-thumb {
    position: relative;
    overflow: hidden;

    border-radius: 8px;
    margin-bottom: 14px;
}

.book-image-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 25%;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: $gold;

    transform: translateY(100%);

    @include transition(transform);

    .book-link:hover &,
    .book-link:focus & {
        transform: translateY(0);
    }
}

.book-image-overlay-text {
    text-transform: uppercase;
    color: $blackish;
    font-size: 18px;
    font-weight: $medium;

    opacity: 0;
    @include transition($property: opacity, $delay: 200ms);

    .book-link:hover &,
    .book-link:focus & {
        opacity: 1;
    }
}

.see-more-btn {
    margin-left: auto;
}

.bestsellers-book-item {
    @media screen and (max-width: ($tablet - 1)) {
        &:nth-child(n + 2) {
            display: none;
        }
    }

    @media screen and (max-width: ($desktop - 1)) {
        &:nth-child(n + 4) {
            display: none;
        }
    }

    @media screen and (max-width: ($desktop-wide - 1)) {
        &:nth-child(n + 5) {
            display: none;
        }
    }
}