body {
  --slider-width: 40px;
  --slider-height: 20px;
  --slider-circle-size: calc(var(--slider-height)  - 2px);
}

.color-scheme-switcher {
  width: var(--slider-width);
  height: var(--slider-height);
  display: inline-block;
  position: relative;
}

#color-scheme-switcher-checkbox {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  position: absolute;
}

#color-scheme-switcher-checkbox:hover, #color-scheme-switcher-checkbox:focus {
  outline: none;
}

.color-switcher-icons {
  right: calc((var(--slider-circle-size)  + 2px) * -1);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.color-switcher-icon-sun {
  opacity: 0;
}

.color-switcher-icon-moon {
  opacity: 1;
  fill: #696969;
  transition: opacity .3s;
}

.color-scheme-switcher-slider {
  cursor: pointer;
  border-radius: var(--slider-circle-size);
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.color-scheme-switcher-slider:before {
  content: "";
  height: var(--slider-circle-size);
  width: var(--slider-circle-size);
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border: none;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.color-scheme-switcher-slider.animated:before {
  transition: all .2s linear;
}

#color-scheme-switcher-checkbox:checked + .color-scheme-switcher-slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

#color-scheme-switcher-checkbox:checked + .color-scheme-switcher-slider:before {
  transform: translate(calc(var(--slider-width)  - var(--slider-circle-size)  - 2px), -50%);
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
}

#color-scheme-switcher-checkbox:checked ~ .color-switcher-icons {
  left: calc((var(--slider-circle-size)  + 2px) * -1);
}

#color-scheme-switcher-checkbox:checked ~ .color-switcher-icons .color-switcher-icon-sun {
  fill: var(--accent-secondary);
  opacity: 1;
  transition: opacity .2s;
}

#color-scheme-switcher-checkbox:checked ~ .color-switcher-icons .color-switcher-icon-moon {
  opacity: 0;
}

