.mobile-menu {
    position: fixed;
    top: 80px;
    width: 100%;
    height: calc(100vh - 64px);
    z-index: $to-top-button-index;
    top: 64px;
    left: 0;
    transform: translate(100%, 0);
    transition: $transition-duration $transition-timing-function;

    @media screen and (min-width: $tablet) {
        display: none;
    }
}

.mobile-menu-open {
    background-color: inherit;
    border: none;

    @media screen and (min-width: $tablet) {
        display: none;
    }
}

.mobile-menu-open__icon {
    stroke: var(--text-color);
}

.mobile-menu-open__icon.is-hidden {
    display: none;
    transition: $transition-duration $transition-timing-function;
}

.mobile-menu-open .mobile-menu__close-icon {
    border: none;
    background-color: inherit;
}

.mobile-menu-open .mobile-menu__close-icon.is-hidden {
    display: none;
}

.mobile-menu-open__icon.is-hidden {
    display: none;
    transition: $transition-duration $transition-timing-function;
    position: absolute;
    right: 20px;
    padding: 0;
    cursor: pointer;
}

.mobile-menu.is-open {
    transform: translate(0, 0);
    transition: $transition-duration $transition-timing-function;
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}

.mobile-menu__container {
    height: 100%;
    width: 100%;
    padding: 20px;
    position: relative;
    top: 0;
    left: 0;
    text-align: center;
    background: radial-gradient(183.6% 183.01% at 59.76% -23.42%,
            #4f2ee8 18.03%,
            #ffffff 100%);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
}

.mobile-menu__container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    min-width: 280px;
    max-width: 280px;
    width: 100%;
    height: 100%;
    background-position: bottom center;
    background-size: contain;
    background-repeat: no-repeat;
    max-width: 100%;
    // background-image: url(../src/images/book-shop-min.png);
}

