@media screen and (min-width: $tablet) {
    .mobile-menu__container {
        padding-top: 140px;
    }
}

.mobile-menu__item {
    position: relative;
    z-index: $to-top-button-index;
}

.mobile-button {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding-top: 15px;
    padding-left: 14px;
    padding-bottom: 14px;
    height: 48px;
    font-weight: $medium;
    font-size: 14px;
    line-height: 1.3;
    gap: 90px;
    color: #ffffff;
    background-color: $blackish;
    border-radius: 18px;
    border: 1.5px solid $blackish;
    transition: $transition-duration $transition-timing-function;
}

.button-modal__logout {
    position: fixed;
    bottom: 20px;
    z-index: $to-top-button-index;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding-top: 15px;
    padding-left: 24px;
    padding-bottom: 15px;
    height: 48px;
    font-weight: $medium;
    font-size: 14px;
    line-height: 1.3;
    gap: 4px;
    color: $blackish;
    background-color: $logo-stroke-light;
    border-radius: 18px;
    border: 1.5px solid $blackish;
    transition: $transition-duration $transition-timing-function;
}

.mobile-menu__btn {
    margin-top: 126px;
    margin-left: auto;
    margin-right: auto;
}

.burger-nav-link {
    padding: 8px 16px;
    width: 72px;
    height: 34px;
    font-weight: $medium;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    background: transparent;
    border-radius: 36px;
    margin-right: 60px;
    color: $milky;

    &:hover,
    &:focus,
    &.active {
        color: $blackish;
        background-color: var(--accent-secondary);
        border-color: $gold;
        font-weight: $bold;
        transition: $transition-duration $transition-timing-function;
    }
}

.mobile-menu__shopping {
    font-weight: $medium;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    margin-top: 17px;
    transition: transform 0.2s ease-in-out;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 7px 16px;
    text-align: center;
    border-radius: 36px;
    box-sizing: border-box;
    gap: 8px;
    color: $milky;

    &:hover,
    :focus,
    &.active {
        background: $gold;
        border-color: $gold;
        color: $blackish;
        fill: currentColor;
        transition: $transition-duration $transition-timing-function;
        font-weight: $bold;
    }
}

.mobile-menu__icon {
    fill: $milky;
    fill: currentColor;
}