.category-books-list {
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;

    @media screen and (min-width: $tablet) {
        flex-flow: row wrap;
        row-gap: 40px;
        column-gap: 25px;
    }

    @media screen and (min-width: $desktop) {
        column-gap: 24px;
    }
}

.books-list-item {
    --gap: 25px;

    @media screen and (min-width: $mobile) {
        @include flex-basis(1, var(--gap));
    }

    @media screen and (min-width: $tablet) {
        @include flex-basis(3, var(--gap));
    }

    @media screen and (min-width: $desktop) {
        // --gap: 24px;
        @include flex-basis(5, var(--gap));
    }
}